button,
input,
select,
textarea {
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
  color: inherit;
  margin: 0;
}
buttonfocus,
inputfocus,
selectfocus,
textareafocus {
  outline: none;
}
input[type="button"],
input[type="submit"],
input[type="reset"] {
  appearance: button;
}
details {
  border: none;
}
details:focus,
details:hover,
details:active {
  outline: none;
}
summary {
  cursor: pointer;
  user-select: none;
}
summary::-webkit-details-marker {
  display: none;
}
summary:focus,
summary:hover,
summary:active {
  outline: none;
}
*,
*:before,
*:after {
  box-sizing: inherit;
  text-rendering: geometricPrecision;
  -webkit-tap-highlight-color: transparent;
}
html {
  font-size: 16px;
}
html,
body {
  background-color: var(--geist-background);
  color: var(--geist-foreground);
}
body {
  font-family: var(--font-sans);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
  font-size: 1rem;
  line-height: 1.5;
  margin: 0;
  padding: 0;
  min-height: 100%;
  position: relative;
  overflow-x: hidden;
}
p,
small {
  letter-spacing: -0.005625rem;
  font-family: var(--font-sans);
  font-weight: 400;
  color: inherit;
  margin: 0;
}
p {
  margin: 1rem 0;
  font-size: 1em;
  line-height: 1.625em;
}
small {
  margin: 0;
  line-height: 1.5;
  font-size: 0.875rem;
}
span {
  font-size: inherit;
  color: inherit;
  font-weight: inherit;
}
b {
  font-weight: 600;
}
img {
  max-width: 100%;
}
a {
  color: var(--geist-link-color);
  text-decoration: none;
  cursor: pointer;
  -webkit-box-align: center;
  align-items: center;
  font-size: inherit;
  -webkit-touch-callout: none;
  -webkit-tap-highlight-color: rgba(0,0,0,0);
}
a:hover {
  text-decoration: none;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: var(--font-sans);
  margin: 0 0 0.625rem 0;
  color: inherit;
}
h1 {
  font-size: 3rem;
  letter-spacing: -0.066875rem;
  line-height: 1.5;
  font-weight: 700;
}
h2 {
  font-size: 2.25rem;
  letter-spacing: -0.020625rem;
  font-weight: 600;
}
h3 {
  font-size: 1.5rem;
  letter-spacing: -0.029375rem;
  font-weight: 600;
}
h4 {
  font-size: 1.25rem;
  letter-spacing: -0.020625rem;
  font-weight: 600;
}
h5 {
  font-size: 1rem;
  letter-spacing: -0.01125rem;
  font-weight: 600;
}
h6 {
  font-size: 0.875rem;
  letter-spacing: -0.005625rem;
  font-weight: 600;
}
selection {
  background-color: var(--geist-selection);
  color: var(--geist-foreground);
}
input-webkit-autofill {
  box-shadow: 0 0 0 100px var(--geist-background) inset;
}
ul,
ol {
  margin: 1rem 0 1rem 0.9375rem;
  color: var(--geist-foreground);
  padding: 0;
  list-style-type: none;
}
ol {
  list-style-type: decimal;
}
li {
  margin-bottom: 0.625rem;
  font-size: 1em;
  line-height: 1.625em;
}
ul li:before {
  content: 'â€“';
  display: inline-block;
  color: var(--accents-4);
  position: absolute;
  margin-left: -0.9375rem;
}
code {
  color: var(--geist-code);
  font-family: var(--font-mono);
  font-size: 0.9em;
  white-space: pre-wrap;
}
code:before {
  content: '`';
}
code:after {
  content: '`';
}
pre {
  border: 1px solid var(--accents-2);
  border-radius: var(--geist-radius);
  padding: calc(var(--geist-gap) * 0.75) var(--geist-gap);
  margin: 1.5rem 0;
  white-space: pre;
  overflow: auto;
  font-size: 0.875rem;
  -webkit-overflow-scrolling: touch;
}
pre code {
  color: var(--geist-foreground);
  font-size: 0.8125rem;
  line-height: 1.25rem;
  white-space: pre;
}
pre code:before,
pre code:after {
  display: none;
}
hr {
  border-color: var(--accents-2);
}
details {
  background-color: var(--accents-1);
}
.zi-title {
  text-transform: uppercase;
  color: var(--accents-4);
}
.zi-subtitle {
  color: var(--accents-3);
  font-size: 0.8125rem;
}

